import React, { Component } from 'react';
import { Link} from 'react-router-dom'
import { Row, Col, Menu, Dropdown, Icon, Popconfirm, message} from 'antd';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux'
import { loginout } from '../../redux/action'
import './index.scss'


const Headerlink = (props) =>{
    const info = props.info||'';
    const actions = props.actions
    if (info) {
        return (
            <Row type="flex" justify="end">
                <Col className="header-item">
                    <Link to="/send"></Link>
                </Col>
                <Col className="header-item">
                    <Popconfirm title="?" onConfirm={confirm} onCancel={cancel} okText="" cancelText="">
                        <a href=":javascript:0;"></a>
                    </Popconfirm>
                </Col>
            </Row>
        )
    }else{
        return (
            <Row type="flex" justify="end">

                <Col className="header-item">
                    <Link to="/login"></Link>
                </Col>
                <Col className="header-item">
                    <Link to="/signin"></Link>
                </Col>

            </Row>
        )
    }
   
    function confirm(e) {
        actions.loginout()
        message.success('');
    }

    function cancel(e) {
        console.log(e);
        message.error('');
    }
}

function createMenu(info){
    if (info) {
        return (
            <Menu>
                <Menu.Item>
                    <Link to="/send"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/home"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/home"></Link>
                </Menu.Item>
            </Menu>
        );
    } else {
        return (
            <Menu>
                <Menu.Item>
                    <Link to="/login"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/signin"></Link>
                </Menu.Item>

            </Menu>
        );

    }
}


 class Header extends Component {

    render() {
        const info = this.props.info|| ''
        const actions = this.props.actions
        const menu = createMenu(info)
        
        return (
            <header className="header-compopents">
                <Row>
                    <Col xs={20} sm={11} offset={1}>
                        <Row type="flex" justify="start">
                            <Col className="header-item">
                                <Link to="/home">
                                    <Icon type="github" />
                                </Link>
                            </Col>
                            <Col className="header-item">
                                <Link to="/"></Link>
                            </Col>
                            {/* <Col className="header-item">
                                <Link to="/markbench"></Link>
                            </Col> */}
                        </Row>
                    </Col>
                    <Col xs={0} sm={11}>
                        <Headerlink info={info} actions={actions}/>
                    </Col>
                    <Col xs={3} sm={0}>
                        <Dropdown overlay={menu} trigger={['click']}>
                            <a className="ant-dropdown-link" href=":javascript:0;">
                                <Icon type="bars" />
                            </a>
                        </Dropdown>
                    </Col>
                </Row>
            </header>
        )
    }
}


//num
function mapStateToProps(state) {
    return { info: state.userReducer.info }
}

//mapDispatchToPropsstoredispatch
function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({ loginout }, dispatch),

    }
}

//Apppropsnumactions4
export default connect(mapStateToProps, mapDispatchToProps)(Header)


